<template>
  <div id="app">
    <h1>{{title}}</h1>
    <input type="text" v-model="newItem" v-on:keyup.enter="addNew">
    <ul>
      <li v-for="item in items" v-bind:key="item.index" v-bind:class="{finished: item.isFinished}" v-on:click="toggleFinish(item)">
        {{ item.label}}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      title: 'this is a todo list',
      items: [
        {label: 'coding', isFinished: false},
        {label: 'walking', isFinished: true},
        {label: 'sleep', isFinished: false},
      ],
      newItem: ''
    }
  },
  methods: {
    toggleFinish: function (item) {
      item.isFinished = !item.isFinished;
      
    },
    addNew: function() {
      let ab = this.newItem;
      this.items.push({
        label: ab,
        isFinished: false
      });
      this.newItem = '';
    }
  }
  ,
  
  components: {
  }
}
</script>

<style>
.finished {
  /* text-decoration: */
  text-decoration: line-through
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%
}
</style>



